<template>
    <div>
        <a-card :bordered="false">
            <a-divider style="margin-bottom: 32px" />
            <a-descriptions title="基础信息" v-if="nvr">
                <a-descriptions-item label="设备名称">{{ nvr.device_name }}</a-descriptions-item>
                <a-descriptions-item label="国标编码ID">{{ nvr.device_id }}</a-descriptions-item>
                <a-descriptions-item label="设备SN">{{ nvr.device_sn }}</a-descriptions-item>
                <a-descriptions-item label="编码类型">{{ nvr.type_code }}</a-descriptions-item>
                <a-descriptions-item label="制造商">{{ nvr.manufacturer }}</a-descriptions-item>
                <a-descriptions-item label="模型">{{ nvr.device_model }}</a-descriptions-item>
                <a-descriptions-item label="固件版本">{{ nvr.firmware }}</a-descriptions-item>
                <a-descriptions-item label="通道数">{{ nvr.channel_num }}</a-descriptions-item>
                <a-descriptions-item label="在线状态">{{ nvr.status_title }}</a-descriptions-item>
                <a-descriptions-item label="本地ip">{{ nvr.local_ip }}</a-descriptions-item>
                <a-descriptions-item label="sip端口号">{{ nvr.local_sip_port }}</a-descriptions-item>
                <a-descriptions-item label="本地http端口号">{{ nvr.local_http_port }}</a-descriptions-item>
                <a-descriptions-item label="本地服务端口号">{{ nvr.local_server_port }}</a-descriptions-item>
                <a-descriptions-item label="公网ip">{{ nvr.net_ip }}</a-descriptions-item>
                <a-descriptions-item label="公网sip端口号">{{ nvr.net_sip_port }}</a-descriptions-item>
                <a-descriptions-item label="公网http端口号">{{ nvr.net_http_port }}</a-descriptions-item>
                <a-descriptions-item label="公网服务端口号">{{ nvr.net_server_port }}</a-descriptions-item>
                <a-descriptions-item label="用户名">{{ nvr.username }}</a-descriptions-item>
                <a-descriptions-item label="密码">{{ nvr.password }}</a-descriptions-item>
                <a-descriptions-item label="设备所在地">{{ nvr.address }}</a-descriptions-item>
                <a-descriptions-item label="合作方">{{ nvr.partner_name }}</a-descriptions-item>
                <a-descriptions-item label="最新上线时间">{{ nvr.lastOnlineTime | filterTime }}</a-descriptions-item>
                <a-descriptions-item label="上次离线时间">{{ nvr.lastOfflineTime | filterTime }}</a-descriptions-item>
                <a-descriptions-item label="创建时间">{{ nvr.createdTime | filterTime }}</a-descriptions-item>
            </a-descriptions>
            <a-divider style="margin-bottom: 32px" />

            <div class="title">设备注册/注销日志</div>
            <s-table style="margin-bottom: 24px" row-key="id" :columns="regColumns" :data="loadRegisterLog">
                <span slot="time" slot-scope="time">{{ time | filterTime(time) }}</span>
                <template slot="operation" slot-scope="text, record">
                    <a-button type="link" size="small" @click="copy(record.data)"> 复制上报数据 </a-button>
                </template>
            </s-table>
            <div class="title">设备上线日志</div>
            <s-table style="margin-bottom: 24px" row-key="id" :columns="actColumns" :data="loadActiveLog">
                <span slot="time" slot-scope="time">{{ time | filterTime(time) }}</span>
            </s-table>
        </a-card>
    </div>
</template>

<script>
import moment from 'moment'
import { STable } from '@/components'
import { message } from 'ant-design-vue'
import { getDeviceRegisterLogs, getDeviceActiveLogs, getNvr } from '@/api/device'
export default {
    data () {
        return {
            nvr: null,
            regColumns: [
                {
                    title: '编号',
                    dataIndex: 'id',
                    key: 'id'
                },
                {
                    title: '是否就绪',
                    dataIndex: 'ready_title',
                    key: 'ready_title'
                },
                {
                    title: '类型',
                    dataIndex: 'type_title',
                    key: 'type_title',
                    filters: [
                        { text: '注册上报', value: 'registered' },
                        { text: '注销上报', value: 'unRegistered' }
                    ],
                    // filteredValue: filteredInfo.type || null,
                    onFilter: (value, record) => {
                        this.regLogQueryParams.type = value
                        return record.type.includes(value)
                    }
                },
                {
                    title: 'IP',
                    dataIndex: 'ipAddress',
                    key: 'ipAddress'
                },
                {
                    title: '发生时间',
                    dataIndex: 'registerTime',
                    key: 'registerTime',
                    scopedSlots: { customRender: 'time' }
                },
                {
                    title: '操作',
                    dataIndex: 'operation',
                    scopedSlots: { customRender: 'operation' }
                }
            ],
            actColumns: [
                {
                    title: '编号',
                    dataIndex: 'id',
                    key: 'id'
                },
                {
                    title: '心跳连续丢失次数',
                    dataIndex: 'lostTimes',
                    key: 'lostTimes'
                },
                {
                    title: '活跃时间',
                    dataIndex: 'keepAliveTime',
                    key: 'keepAliveTime',
                    scopedSlots: { customRender: 'time' }
                }
            ],
            regLogQueryParams: {
                nvrId: '',
                type: ''
            },
            actLogQueryParams: {
                nvrId: ''
            },
            loadRegisterLog: (parameter) => {
                parameter.nvrId = this.id
                this.regLogQueryParams = Object.assign(this.regLogQueryParams, parameter)

                return this.getDeviceRegisterLogs()
            },
            loadActiveLog: (parameter) => {
                parameter.nvrId = this.id

                return getDeviceActiveLogs(parameter)
            }
        }
    },
    components: {
        STable
    },
    mounted () {
        this.loadData()
    },
    methods: {
        async loadData () {
            this.getNvr()
        },
        async getNvr () {
            this.nvr = await getNvr(this.id)
        },
        getDeviceRegisterLogs () {
            return getDeviceRegisterLogs(this.regLogQueryParams)
        },
        copy (copyContent) {
            const textareaEl = document.createElement('textarea')
            textareaEl.setAttribute('readonly', 'readonly')
            textareaEl.value = copyContent
            document.body.appendChild(textareaEl)
            textareaEl.select()
            const isSuccess = document.execCommand('copy')
            textareaEl.style.display = 'none'
            textareaEl.remove()
            message.success('复制成功!!!')
            return isSuccess
        }
    },
    filters: {
        filterTime (time) {
            return moment(time).format('YYYY-MM-DD HH:mm')
        },
        statusFilter (status) {
            const statusMap = {
                processing: '进行中',
                success: '完成',
                failed: '失败'
            }
            return statusMap[status]
        }
    },
    computed: {
        title () {
            return this.$route.meta.title
        },
        id () {
            return this.$route.params.id
        }
    }
}
</script>

<style lang="less" scoped>
.title {
    color: rgba(0, 0, 0, 0.85);
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 16px;
}
</style>
